* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: "Avenir";
  overflow-x: hidden;
  max-width: 100vw;
}

html {
  scroll-behavior: smooth;
}

.background {
  text-align: center;
  color: var(--color);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url(./public/assets/visuels/background_site_kickoff.webp);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  overflow: scroll;
}

.background-black {
  background-color: black;
}

#padding-bot {
  padding-bottom: 0.5rem;
}

.section-one {
  position: relative;
  height: 100vh;
  width: 100vw;
  color: white;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

.container-one {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 50vh;
}

.section-two {
  position: relative;
  height: 100vh;
  width: 100vw;
  color: white;
}

.section-two .part-one {
  height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #786abf;
  border-bottom: 1px solid white;
}

.section-two .part-two {
  height: 60vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #13120d;
}

.section-three {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  background: #786abf;
}

.section-three .part-one {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.section-three .part-two {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
}

.section-three span {
  color: black;
}

.section-four {
  height: 100vh;
  width: 100vw;
  color: white;
}

.container-four {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: flex-start;
  padding: 1%;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.footer div {
  justify-content: space-evenly;
}
.footer img {
  height: 2rem;
  padding-left: 2rem;
}

.footer.show {
  opacity: 1;
}

.logo-date {
  height: 4rem;
}

#circle {
  opacity: 0;
}

.st1 {
  height: 10rem;
}

.logo-place {
  height: 15rem;
}

.logo-metro {
  height: 1.5rem;
  vertical-align: -6px;
}

a {
  text-decoration: none;
}

.link {
  color: white;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 3rem;
}

time {
  font-size: 2rem;
}

#first-text {
  opacity: 0;
}

#second-text {
  opacity: 0;
}

#first-ornament-svg {
  position: absolute;
  left: -10px;
  bottom: 100px;
  height: 10vh;
  width: 10vh;
}

#second-ornament-svg {
  position: absolute;
  right: -2%;
  top: 50px;
  height: 15vh;
  width: 15vh;
}

#third-ornament-svg {
  position: absolute;
  right: -15px;
  bottom: 50px;
  height: 4rem;
  z-index: 1000;
  rotate: 110deg;
}
#fourth-ornament-svg {
  position: absolute;
  left: -10px;
  bottom: 10px;
  height: 4rem;
  z-index: 1000;
  rotate: 110deg;
}
#fifth-ornament-svg {
  position: absolute;
  right: -6rem;
  bottom: 30%;
  height: 13rem;
  z-index: 1000;
  rotate: 110deg;
}

#titre {
  padding-top: 10;
  height: 15%;
  opacity: 0;
}

#date {
  padding-top: 10;
  height: 15%;
  opacity: 0;
}
#arrow-down {
  height: 5vh;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
  cursor: pointer;
}
#arrow-up {
  height: 5vh;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  cursor: pointer;
  padding-right: 0.5rem;
}

.arrow-container {
  position: fixed;
  bottom: 1%;
  right: 2%;
  display: flex;
  flex-direction: row;
  z-index: 1000;
}

/* Rebours */

.splitflap {
  position: relative;
  min-width: 50px;
  height: 50px;
  margin: 5px;
  line-height: 50px;
  font-size: 50px;
  font-family: Monospace;
  text-align: center;
  color: white;
}

.center {
  opacity: 0;
  left: 0;
  top: 50%;
  margin-top: 2%;
  width: 100%;
  display: flex;
  justify-content: center;
  perspective: 500px;
}

.top {
  position: relative;
  height: 50%;
  width: 100%;
  background-color: #000;
  border-radius: 10px 10px 0 0;
  overflow: hidden;
  z-index: 0;
}

.bottom {
  position: relative;
  height: 100%;
  width: 100%;
  margin-top: -50%;
  border-radius: 10px 10px 10px 10px;
  z-index: -1;
  background-color: black;
  background-image: linear-gradient(#000, #000);
  transform-origin: center;
}

.nextHalf {
  position: relative;
  height: 50%;
  width: 100%;
  margin-top: -100%;
  overflow: hidden;
  border-radius: 10px 10px 0 0;
  z-index: 2;
  background-color: black;
  background-image: linear-gradient(#000, #000);
  transform-origin: bottom;
}

.nextFull {
  position: relative;
  height: 100%;
  width: 100%;
  background-color: #000;
  margin-top: -50%;
  border-radius: 10px 10px 10px 10px;
  z-index: -3;
}

.flip1 {
  animation: flip1 ease-in 1;
  animation-duration: 1s;
}

.flip2 {
  animation: flip2 ease-out 1;
  animation-duration: 1s;
}

@keyframes flip1 {
  0% {
    transform: rotateX(0deg);
    background-color: #000;
  }
  50% {
    transform: rotateX(90deg);
    background-color: black;
  }
  100% {
    transform: rotateX(90deg);
  }
}

@keyframes flip2 {
  0% {
    transform: rotateX(-90deg);
  }
  50% {
    transform: rotateX(-90deg);
  }
  100% {
    transform: rotateX(0deg);
    background-color: #000;
  }
}

.hidden {
  opacity: 0;
  filter: blur(5px);
  transform: translateY(100%);
  transition: all 1s;
}

.show {
  opacity: 1;
  filter: blur(0);
  transform: translateY(0);
}

@media screen and (max-width: 1024px) {
  p {
    padding: 0.5rem;
  }
  .logo-osrd-big {
    height: 13rem;
    padding: 0.5rem;
  }
  .logo-date {
    height: 2rem;
    padding: 0.5rem;
  }
  .logo-titre {
    height: 4rem;
    padding: 0.5rem;
  }
  h2 {
    font-size: 3rem;
  }
  .splitflap {
    position: relative;
    min-width: 25px;
    height: 25px;
    margin: 2.5px;
    line-height: 25px;
    font-size: 25px;
    font-family: Monospace;
    text-align: center;
    color: white;
  }

  .center {
    left: 0;
    top: 50%;
    margin-top: 2%;
    width: 100%;
    display: flex;
    justify-content: center;
    perspective: 500px;
  }

  .top {
    position: relative;
    height: 50%;
    width: 100%;
    background-color: #000;
    border-radius: 5px 5px 0 0;
    overflow: hidden;
    z-index: 0;
  }

  .bottom {
    position: relative;
    height: 100%;
    width: 100%;
    margin-top: -50%;
    border-radius: 5px 5px 5px 5px;
    z-index: -1;
    background-color: black;
    background-image: linear-gradient(#000, #000);
    transform-origin: center;
  }

  .nextHalf {
    position: relative;
    height: 50%;
    width: 100%;
    margin-top: -100%;
    overflow: hidden;
    border-radius: 5px 5px 0 0;
    z-index: 2;
    background-color: black;
    background-image: linear-gradient(#242323, #000);
    transform-origin: bottom;
  }

  .nextFull {
    position: relative;
    height: 100%;
    width: 100%;
    background-color: #000;
    margin-top: -50%;
    border-radius: 5px 5px 5px 5px;
    z-index: -3;
  }
  #fifth-ornament-svg {
    position: absolute;
    right: -7rem;
    top: 30%;
    height: 8rem;
    z-index: 1000;
    rotate: 110deg;
  }
}

@media (prefers-reduced-motion) {
  .hidden {
    transition: none;
  }
}
